.about-main {
  overflow: hidden;
  position: relative;
  margin-top: s(89);
  padding-bottom: s(70);
  & svg {
    position: absolute;
    right: s(-68);
    top: s(21);
    @include block(s(1683), s(400));
  }
  &__title {
    margin-left: s(-5);
    margin-bottom: s(92);
    font-size: s(82);
    font-weight: 700;
    color: var(--red);
  }
  &__slogan {
    bottom: s(-40);
    right: s(-78);
  }
  &__img {
    @include img(s(535), s(350));
  }
  &__content {
    @include flex-50-y();
    gap: s(40);
  }
  &__desc {
    margin-top: s(2);
    font-family: 'GraphikLCG';
    font-weight: 300;
    font-size: s(22);
    line-height: 131%;
    letter-spacing: 0.015em;
    & strong {
      font-weight: 600;
    }
  }
  @include mobile {
    margin-top: s-md(56);
    padding-bottom: s-md(33);
    & svg {
      top: s-md(5);
      right: s-md(8);
      @include block(s-md(209), s-md(149));
    }
    &__title {
      margin-left: 0;
      margin-bottom: s-md(26);
      font-size: s-md(29);
    }
    &__img {
      @include block(100%, s-md(224));
      border-radius: s-md(10);
    }
    &__content {
      flex-direction: column;
      gap: s-md(23);
    }
    &__slogan {
      right: s-md(-54);
    }
    &__desc {
      font-size: s-md(18);
      line-height: 128%;
      letter-spacing: 0.034em;
    }
  }
}
