.smartmoney-investment {
  &__title,
  &__tabs {
    position: relative;
    z-index: 5;
  }
  &__title {
    text-transform: uppercase;
    margin-bottom: s(48.5);
    font-weight: 700;
    font-size: s(60);
    line-height: 110%;
    color: #898a8e;
    & .underline {
      border-bottom: s(3) solid var(--white);
      color: var(--white);
    }
  }
  &__tab {
    @include flex-50-50();
    text-transform: uppercase;
    text-align: center;
    width: 100%;
    min-height: s(122);
    border-radius: s(10);
    font-size: s(20);
    font-weight: 700;
    line-height: 160%;
    letter-spacing: 0.04em;
    background-image: url('../img/texture.svg');
    @include transition(background-color, var(--red));
    & span {
      width: 100%;
      max-width: s(285);
    }
    &:hover,
    &:active,
    &.active {
      background-color: var(--grey);
    }
    &s {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(s(304), 1fr));
      gap: s(40);
    }
    &-container {
      grid-column: 1/6;
      position: relative;
      min-height: s(301);
      padding: s(41);
      border-radius: s(10);
      background: url('../img/texture.svg'), var(--grey);
      background-size: cover;
    }
    &-box {
      display: none;
      gap: s(40);
      &.active {
        display: flex;
      }
    }
  }
  &__dropdown {
    display: none;
    height: 100%;
    padding: s(39) s(40) s(40) s(75);
    will-change: height;
    & > p {
      position: relative;
      font-family: 'GraphikLCG';
      width: 100%;
      max-width: s(393);
      font-size: s(22);
      font-weight: 400;
      line-height: 130%;
      & > svg {
        position: absolute;
        top: s(4);
        left: s(-38);
        @include block(s(16));
      }
      &:not(:last-child) {
        margin-bottom: s(25);
      }
    }
    &,
    &-btn {
      position: relative;
      width: 100%;
      border-radius: s(10);
      border: s(1) solid var(--white);
    }
    &--all {
      position: absolute;
      @include flex-50-50();
      visibility: visible;
      opacity: 1;
      bottom: s(39);
      right: s(36);
      @include block(s(53));
      border: s(0.5) solid var(--white);
      border-radius: 100%;
      @include transition((opacity, visibility));
      & svg {
        @include block(s(25));
        transform: rotate(90deg);
      }
      &.hidden {
        visibility: hidden;
        opacity: 0;
      }
    }
    &-box {
      display: flex;
      flex-direction: column;
      flex-basis: calc(100% - s(40) * 2);
    }
    &-btn {
      min-height: s(127);
      margin-bottom: s(10);
      &.active {
        & i svg {
          transform: rotate(0deg);
        }
      }
      & strong {
        display: inline-block;
        text-align: center;
        text-transform: uppercase;
        width: 100%;
        max-width: s(260);
        font-size: s(18);
        font-weight: 700;
        line-height: 160%;
        letter-spacing: 0.04em;
      }
      & i {
        @include flex-50-50();
        @include block(s(40));
        position: absolute;
        border-radius: 100%;
        border: s(0.5) solid var(--white);
        bottom: s(21);
        right: s(18);
        & svg {
          @include block(s(11));
          @include transition(transform, rotate(95deg));
        }
      }
    }
  }
  @include mobile {
    &__title {
      font-size: s-md(25);
    }
    &__tab {
      &s {
        grid-template-columns: initial;
      }
    }
  }
}
