.private-equity {
  padding-top: s(190);
  &__title {
    text-transform: uppercase;
    margin-bottom: s(55);
    font-size: s(60);
    font-weight: 400;
    line-height: 110%;
    & span {
      color: #898a8e;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    padding-bottom: s(50);
    &--left {
      position: relative;
      display: flex;
      align-items: flex-end;
      & svg {
        position: absolute;
        left: s(-115);
        bottom: s(-17);
        @include block(s(653), s(299));
      }
    }
    &--right {
      width: 100%;
      max-width: s(1109);
      border-top: s(2) solid var(--white);
      padding-top: s(50);
    }
  }
  &__link {
    padding: s(6) s(26);
    font-size: s(14);
    color: var(--red);
    @include transitions((border-color, background-color, color));
    &:hover,
    &:active {
      border-color: var(--red);
      background-color: var(--red);
      color: var(--white);
    }
  }
  &__desc {
    font-family: 'GraphikLCG';
    font-size: s(22);
    font-weight: 400;
    line-height: 131%;
  }
  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(s(534), 1fr));
    gap: s(40);
  }
  &__card {
    position: relative;
    overflow: hidden;
    min-height: s(350);
    border-radius: s(10) s(10) 0 0;
    padding: s(50);
    background: url('../img/texture.svg'), var(--grey);
    background-size: 200%;
    &:nth-child(2) span {
      bottom: s(-156);
    }
    & span {
      position: absolute;
      bottom: s(-140);
      left: 0;
      @include block(100%);
      background-size: 100%;
      background-repeat: no-repeat;
    }
    &-title {
      text-transform: uppercase;
      margin-bottom: s(30);
      font-size: s(18);
      font-weight: 700;
      line-height: s(25);
    }
    &-desc {
      font-family: 'GraphikLCG';
      font-size: s(22);
      font-weight: 400;
      line-height: 131%;
      letter-spacing: 0.02em;
    }
  }
}
